во. На всех заводах по выпуску добавок установлено австрийское оборудование и реализован закрытый производственный цикл, что обеспечивает полное отсутствие отходов и выбросов в атмосферу. Суммарный объем производства всех заводов по добавкам составляет около 85 тыс. тонн в год.")]),_c('p',[_vm._v("На территории всех заводов действуют производственные лаборатории, оборудованная всем необходимым для оперативного контроля качества и проведения НИОКР по адаптации рецептур под требования местных заказчиков. Благодаря новым производствам, удалось не только расширить географию поставок во все регионы России, но и сократить логистические расходы.")])])])])])])])]),_c('div',{staticClass:"layoutcontainer section"},[_c('div',[_c('div',{staticClass:"cmp-layout grid-container"},[_c('div',{staticClass:"cmp-layout__content grid-x grid-margin-x"},[_c('div',{staticClass:"cell small-12 medium-6 large-6"},[_c('div',{staticClass:"textimage section"},[_c('div',{staticClass:"cmp-textimage cmp-text cmp-textimage__list-bullet"},[_c('h2',[_vm._v("Поликарбоксилатные эфиры")]),_c('p',[_vm._v("В 2015 году Sika запустила уникальное для России производство поликарбоксилатных эфиров ключевого сырья для пластифицирующих добавок в бетон.")]),_c('p',[_vm._v("При производстве ПКЭ применяется уникальная технология этерефикации, разработанная и запатентованная специалистами концерна Sika. Мощность завода – 12 000 тонн поликарбоксилатных эфиров ежегодно, что позволяет выпускать более чем 50 000 тонн добавок в бетон. Выпуск поликарбоксилатов на новом предприятии значительно уменьшил критическую зависимость нашей страны от импорта сырья в таких сегментах рынка, как производство добавок в бетон и интенсификаторов помола цемента, неотъемлемых составляющих любой современной стройки. Запуск завода ПКЭ в Лобне позволил Sika не только полностью заместить импорт поликарбоксилатов, но и начать их экспорт в страны ближнего зарубежья.")]),_c('p',[_vm._v("В производстве ПКЭ в Лобне используется российское сырье, отобранное в соответствии со стандартами Sika. Sika приносит в Россию современные производственные решения: оборудование и технологии по выпуску поликарбоксилатных эфиров соответствует передовым мировым образцам.")])])])]),_c('div',{staticClass:"cell small-12 medium-6 large-6"},[_c('div',{staticClass:"textimage section"},[_c('div',{staticClass:"cmp-textimage cmp-textimage__list-bullet"},[_c('div',{staticClass:"cmp-image-- cmp-image"},[_c('picture',{attrs:{"data-image-src":"{\"isResponsive\":true,\"pattern\":\"https://sika.scene7.com/is/image/sika/ru-factories-polikarboxilaty${imageProfile}?fit\\u003dcrop%2C1\\u0026wid\\u003d${width}\\u0026hei\\u003d${height}\\u0026fmt\\u003d${format}\",\"originalWidth\":1000,\"originalHeight\":667,\"alt\":\"Поликарбоксилатные эфиры\",\"imageProfileNamePrefix\":\":\",\"imageProfileMap\":{}}","data-height-based":"false"}},[_c('source',{attrs:{"type":"image/webp","srcset":"https://sika.scene7.com/is/image/sika/ru-factories-polikarboxilaty?fit=crop%2C1&wid=600&hei=400&fmt=webp-alpha" + " 1x, " + "https://sika.scene7.com/is/image/sika/ru-factories-polikarboxilaty?fit=crop%2C1&wid=960&hei=640&fmt=webp-alpha" + " 1.5x"}}),_c('source',{attrs:{"type":"image/png","srcset":"https://sika.scene7.com/is/image/sika/ru-factories-polikarboxilaty?fit=crop%2C1&wid=600&hei=400&fmt=png-alpha" + " 1x, " + "https://sika.scene7.com/is/image/sika/ru-factories-polikarboxilaty?fit=crop%2C1&wid=960&hei=640&fmt=png-alpha" + " 1.5x"}}),_c('img',{staticClass:"cmp-image__image",attrs:{"src":"https://sika.scene7.com/is/image/sika/ru-factories-polikarboxilaty?fit=crop%2C1&wid=600&hei=400&fmt=","alt":"Поликарбоксилатные эфиры"}})])])])])])])])])]),_c('div',{staticClass:"layoutcontainer section"},[_c('div',[_c('div',{staticClass:"cmp-layout grid-container"},[_c('div',{staticClass:"cmp-layout__content grid-x grid-margin-x"},[_c('div',{staticClass:"cell small-12 medium-6 large-6"},[_c('div',{staticClass:"textimage section"},[_c('div',{staticClass:"cmp-textimage cmp-textima