. тон в год.")]),_c('p',[_vm._v("На территории заводов функционируют производственные лаборатории, что позволяет своевременно осуществлять контроль качества продукции. Установленное на производствах итальянское оборудование и закрытый производственный цикл обеспечивает отсутствие выбросов в атмосферу.")]),_c('p',[_vm._v("Заводы в Центральном и Южном федеральном округах позволяют осуществлять реализацию продуктов по всей России в короткие сроки. Время обработки запросов клиентов не превышает двух часов.")])])])])])])])]),_c('div',{staticClass:"layoutcontainer section"},[_c('div',[_c('div',{staticClass:"cmp-layout grid-container"},[_c('div',{staticClass:"cmp-layout__content grid-x grid-margin-x"},[_c('div',{staticClass:"cell small-12 medium-6 large-6"},[_c('div',{staticClass:"textimage section"},[_c('div',{staticClass:"cmp-textimage cmp-text cmp-textimage__list-bullet"},[_c('h2',[_vm._v("Эпоксидные покрытия")]),_c('p',[_vm._v("С 2017 года Sika запустила в Лобне высокотехнологичное производство напольных эпоксидных покрытий. Таким образом, удалось локализовать в Подмосковье выпуск продукции, поставляемой ранее из-за рубежа. Эпоксидные составы, выпускаемые на заводе, поставляются на строительные объекты России и стран СНГ.")]),_c('p',[_vm._v("Полимерные составы, выпускаемые на производстве, широко используются в строительной индустрии для устройства и ремонта полов на различных объектах. В последние годы область применения полимерных полов значительно расширилась – технология применяется не только на промышленных объектах, но и активно используется в индустриальной, коммерческой и даже жилой недвижимости.")]),_c('p',[_vm._v("Компания Sika уже более 10 лет занимается поставкой напольных полимерных покрытий на российский рынок. За это время материалы компании были использованы на десятках крупнейших объектов по всей России. По объемам поставок полимерных напольных покрытий компания Sika входит в пятерку лидеров российского рынка. Ранее продукция концерна использовалась дл